"use strict";
cc._RFpush(module, 'ec5ccXiT0FLN6b4svuC5qn3', 'Helpers');
// scripts/utils/Helpers.js

if (CC_JSB && cc.runtime) {
    // fix cocos-creator/fireball#3578
    cc.LoaderLayer.setUseDefaultSource(false);
    cc.Dialog.setUseDefaultSource(false);
}

// Returns a random integer between min (included) and max (excluded)
function getRandomInt(min, max) {
    return Math.floor(Math.random() * (max - min)) + min;
}

function saveLocal(key, value) {
    return cc.sys.localStorage.setItem(key, value);
}

function loadLocal(key) {
    return cc.sys.localStorage.getItem(key);
}

module.exports = {
    getRandomInt: getRandomInt,
    saveLocal: saveLocal,
    loadLocal: loadLocal
};

cc._RFpop();